.label-feed__input {
  border: 1px solid #cdcdcd;
  width: 202px;
  height: 45px;
  margin-left: 13px;
  padding-left: 15px;
}

.label-feed__email {
  background-image: url("../img/form-email.svg");
  background-repeat: no-repeat;
  background-position: center right 14px;
}

.label-feed__tel {
  background-image: url("../img/form-tel.svg");
  background-repeat: no-repeat;
  background-position: center right 14px;
}
