@use "../variables.scss" as variables;

.social-list {
  display: flex;
  width: 132px;
  justify-content: space-between;
  padding: 20px 74px 20px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: variables.$tablet-width) {
    border: 0;
  }
}

.social-list__item {
  list-style-type: none;
}
